<template>
  <div>


    <div class="operation">

      <el-button type="danger" plain @click="delBatch"></el-button>
    </div>

    <div class="table">
      <el-table :data="tableData" strip @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center"></el-table-column>

        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="time" label=""></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="ip" label="ip"></el-table-column>
        <el-table-column label="" align="center" width="180">
          <template v-slot="scope">

            <el-button size="small" type="danger" plain @click="del(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="pagination">
        <el-pagination
            background
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-sizes="[5, 10, 20]"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>
    </div>

    <el-dialog title="" v-model="fromVisible" width="40%" :close-on-click-modal="false" destroy-on-close>
      <el-form :model="form" label-width="100px" style="padding-right: 50px" :rules="rules" ref="formRef">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="time">
          <el-input v-model="form.time" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="username">
          <el-input v-model="form.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="ip" prop="ip">
          <el-input v-model="form.ip" placeholder="ip"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
      <div  class="dialog-footer">
        <el-button @click="fromVisible = false"> </el-button>
        <el-button type="primary" @click="save"> </el-button>
      </div>
      </template>
    </el-dialog>


  </div>
</template>
<script>
import request from "@/utils/request";

export default {
  name: "LogView",
  data() {
    return {
      tableData: [],  // 
      pageNum: 1,   // 
      pageSize: 10,  // 
      total: 0,
      name: null,
      fromVisible: false,
      form: {},
      user: JSON.parse(localStorage.getItem('user') || '{}'),
      rules: {
      },
      ids: []
    }
  },
  created() {
    this.load(1)
  },
  methods: {


    save() {   //   

      this.$refs.formRef.validate((valid) => {
        if (valid) {
          request({
            url: this.form.id ? '/log/update' : '/log/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form
          }).then(res => {
            if (res.code === '1') {  // 
              this.$message.success('')
              this.load(1)
              this.fromVisible = false
            } else {
              this.$message.error(res.msg)  // 
            }
          })
        }
      })
    },
    del(id) {   // 
      // eslint-disable-next-line no-unused-vars
      this.$confirm('', '', {type: "warning"}).then(response => {
        request.delete('/log/delete/' + id).then(res => {
          if (res.code === '1') {   // 
            this.$message.success('')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 
          }
        })
      }).catch(() => {
      })
    },
    handleSelectionChange(rows) {   // 
      this.ids = rows.map(v => v.id)
    },
    delBatch() {   // 
      if (!this.ids.length) {
        this.$message.warning('')
        return
      }
      // eslint-disable-next-line no-unused-vars
      this.$confirm('', '', {type: "warning"}).then(response => {
       request.delete('/log/delete/batch', {data: this.ids}).then(res => {
          if (res.code === '1') {   // 
            this.$message.success('')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 
          }
        })
      }).catch(() => {
      })
    },
    load(pageNum) {  // 
      if (pageNum) this.pageNum = pageNum
      request.get('/log/selectPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          name: this.name,
        }
      }).then(res => {
        if (res.code === '1') {
          this.tableData = res.data.list
          this.total = res.data.total
        } else {
          this.$message.error(res.msg)
        }
      })
    },
    reset() {
      this.name = null
      this.load(1)
    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
  }
}
</script>

<style scoped>

</style>