<template>
  <div>
    <div class="search">
      <el-input placeholder="" style="width: 200px" v-model="name"></el-input>
      <el-button type="info" plain style="margin-left: 10px" @click="load(1)"></el-button>
      <el-button type="warning" plain style="margin-left: 10px" @click="reset"></el-button>
    </div>

    <div class="operation">
      <el-button type="primary" plain @click="handleAdd"></el-button>
      <el-button type="danger" plain @click="delBatch"></el-button>
    </div>

    <div class="table">
      <el-table :data="tableData" strip @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center"></el-table-column>

        <el-table-column prop="uid" label="id" sortable></el-table-column>
        <el-table-column prop="chapterid" label="id"></el-table-column>
        <el-table-column prop="content" label=""></el-table-column>
        <el-table-column prop="creattime" label="" sortable></el-table-column>
        <el-table-column label="" align="center" width="180">
          <template v-slot="scope">
            <el-button size="small" type="primary" plain @click="handleEdit(scope.row)"></el-button>
            <el-button size="small" type="danger" plain @click="del(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="pagination">
        <el-pagination
            background
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-sizes="[5, 10, 20]"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>
    </div>

    <el-dialog title="" v-model="fromVisible" width="40%" :close-on-click-modal="false" destroy-on-close>
      <el-form :model="form" label-width="100px" style="padding-right: 50px" :rules="rules" ref="formRef">
        <el-form-item label="id" prop="uid">
          <el-input v-model="form.uid" placeholder="id"></el-input>
        </el-form-item>
        <el-form-item label="id" prop="chapterid">
          <el-input v-model="form.chapterid" placeholder="id"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input v-model="form.content" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
      <div  class="dialog-footer">
        <el-button @click="fromVisible = false"> </el-button>
        <el-button type="primary" @click="save"> </el-button>
      </div>
      </template>
    </el-dialog>


  </div>
</template>
<script>
import request from "@/utils/request";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Comment",
  data() {
    return {
      tableData: [],  // 
      pageNum: 1,   // 
      pageSize: 10,  // 
      total: 0,
      name: null,
      fromVisible: false,
      form: {},
      user: JSON.parse(localStorage.getItem('user') || '{}'),
      rules: {
      },
      ids: []
    }
  },
  created() {
    this.load(1)
  },
  methods: {
    handleAdd() {   // 
      this.form = {}  // 
      this.fromVisible = true   // 

    },
    handleEdit(row) {   // 
      this.form = JSON.parse(JSON.stringify(row))  // form  
      this.fromVisible = true   // 
    },
    save() {   //   
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          request({
            url: this.form.id ? '/comment/update' : '/comment/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form
          }).then(res => {
            if (res.code === '1') {  // 
              this.$message.success('')
              this.load(1)
              this.fromVisible = false
            } else {
              this.$message.error(res.msg)  // 
            }
          })
        }
      })
    },
    del(id) {   // 
      // eslint-disable-next-line no-unused-vars
      this.$confirm('', '', {type: "warning"}).then(() => {
        request.delete('/comment/delete/' + id).then(res => {
          if (res.code === '1') {   // 
            this.$message.success('')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 
          }
        })
      }).catch(() => {
      })
    },
    handleSelectionChange(rows) {   // 
      this.ids = rows.map(v => v.id)
    },
    delBatch() {   // 
      if (!this.ids.length) {
        this.$message.warning('')
        return
      }

      this.$confirm('', '', {type: "warning"}).then(() => {
        request.delete('/comment/delete/batch', {data: this.ids}).then(res => {
          if (res.code === '1') {   // 
            this.$message.success('')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 
          }
        })
      }).catch(() => {
      })
    },
    load(pageNum) {  // 
      if (pageNum) this.pageNum = pageNum
      request.get('/comment/selectPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          name: this.name,
        }
      }).then(res => {
        if (res.code === '1') {
          this.tableData = res.data.list
          this.total = res.data.total
        } else {
          this.$message.error(res.msg)
        }
      })
    },
    reset() {
      this.name = null

    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
  }
}
</script>

<style scoped>

</style>